import empty from '@/components/empty.vue';
import axios from '@/utils/uniAxios.js';
export default {
	components:{
		empty
	},
	data() {
		return {
			src:'../static/my/my-address.png',
			msg:'你还没有任何地址，赶紧添加吧',
			source: 0,
			addressList: [],
			id:'',
			checked:false,
			isToAddress:false,
			radio:''
		}
	},
	onLoad(option) {
		this.id = option.id;
		this.source = option.source;
		this.getAddress();
		if(option.postOrder){//标明是从提交订单页面进入的
			this.isToAddress = true;
		}
	},
	methods: {
		// 选择地址
		select(e){
			this.radio = e.detail;
			uni.setStorageSync('addressMsg',JSON.stringify(this.addressList[this.radio]))
			
			setTimeout(()=>{
				uni.redirectTo({
					url:'../shopcar/postOrder'
				})
			})
			
		},
		// 获取所有地址列表
		getAddress(){
			let obj = {
				id:this.id
			}
			axios.post('/member/address/list').then(res=>{
				if(res.data.code==200){
					this.addressList = res.data.data;
				}
			})
		},
		//选择地址
		checkAddress(item) {
			if (this.source == 1) {
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		// 添加收货地址
		addAddress(type, item) {
			uni.navigateTo({
				url: `/pages/set/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type) {
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.getAddress()
			// this.addressList.unshift(data);
		}
	}
}
